  box-sizing: border-box;
}

body {
  height: fit-content;
  background: #05051f;
  background: linear-gradient(0deg, 0%, 34%, rgb(116, 116, 116) 100%);
  background-image: linear-gradient(rgb(62, 0, 119), rgb(255, 208, 0));
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

@font-face {
  font-family: 'retroFont';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
}

@font-face {
  font-family: 'w95';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('woff');
}

@font-face {
  font-family: 'w96';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_2___}) format('woff2');
}

#spinningLines {
  z-index: -1;
  margin-top: 10vh;
  width: min(80vh, 80vw);
  height: min(80vh, 80vw);
  position: absolute;
  bottom: -30vh;
}

.rotate {
  animation: rotation 100s infinite linear;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

.rotate2 {
  animation: rotation2 110s infinite linear;
}

@keyframes rotation2 {
  from {
    transform: rotate(359deg);
  }
  to {
    transform: rotate(0deg);
  }
}

#titleDiv {
  margin-bottom: 0vh;
  margin-top: 20vh;
  text-align: center;
}

.line div {
  margin-bottom: 2vh;
}

#locationBtn {
  font-family: 'retroFont';
  color: white;
  text-decoration: underline;
}

#wordArt {
  width: 70vw;
  height: auto;
}

#textLines {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: calc(20vw - 60px);
  margin-right: calc(20vw - 60px);
}

* {
  font-family: 'retroFont';
  font-size: clamp(1.8rem, 4vh, 4rem);
  color: rgb(255, 248, 236);
  text-shadow:
    0px 0 0 #000,
    0 -0px 0 #000,
    0 0px 0 #000,
    -0px 0 0 #000;
  user-select: none;
}

.line {
  align-self: flex-start;
  min-height: 1em;
}

button {
  border: 1px solid rgb(63, 56, 56);
  background-color: rgb(129, 129, 129);
  box-shadow:
    2px 2px 0px black,
    2px 2px 0px inset rgb(255, 248, 236);
  max-width: fit-content;
  display: flex;
  flex-direction: row;
  color: black;
  font-family: 'w95';
  font-weight: lighter;
}

button:hover {
  background-color: rgb(114, 114, 114);
  outline: 2px dotted black;
  outline-offset: -7px;
}

button:active {
  box-shadow: inset 2px 2px 0px black;
  border: 0px 0px;
}

.w95Explorer {
  z-index: 2;
  position: absolute;

  background-color: rgb(175, 175, 175);
  height: auto;
  box-shadow:
    2px 2px 0px black,
    2px 2px 0px inset rgb(255, 248, 236);
  padding: 0.02em;
}

.w95Explorer * {
  font-size: clamp(0.8rem, 2.2vh, 2.2rem);
  font-family: 'w95';
}

.w95Explorer header {
  background-color: rgb(0, 0, 175);
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-height: 1.2em;
  padding: 0.2em;

  min-height: 1em;
}

.w95Explorer header > * {
  max-height: 0.8em !important;
  width: auto;
  overflow: hidden;
}

.content {
  height: fit-content;
  width: fit-content;
  box-shadow:
    0 0 0 1px rgb(31, 31, 31),
    0 0 0 3px rgb(68, 68, 68),
    0 0 0 5px rgb(109, 109, 109),
    0 0 0 7px rgb(131, 131, 131);
}

.w95Explorer main {
  max-width: 100%;
  background-color: rgb(175, 175, 175);
  padding: 1em;
  height: auto;
  padding-bottom: 0.3em;
}

.w95Explorer main * {
  max-width: 100%;
  box-sizing: border-box;
  overflow-wrap: break-word;
}

.w95Explorer footer {
  background-color: rgb(175, 175, 175);
  display: flex;
  flex-direction: column;
  gap: 0.5em;
  height: fit-content;
  padding: 0.2em;
  padding-left: 1em;
  padding-right: 1em;
}

#w95Content {
  width: 100%;
  height: 0%;
}

#w95Content div {
  background-color: blue;
  font-family: 'w95';
}

.button {
  background-color: rgb(175, 175, 175);
  box-shadow:
    2px 2px 0px black,
    2px 2px 0px inset rgb(255, 248, 236);
  padding: 0.02em;
}

.button:hover {
  background-color: rgb(114, 114, 114);
  outline: 2px dotted black;
  outline-offset: -7px;
}

.button {
  background-color: rgb(129, 129, 129);
  box-shadow:
    3px 4px 0px black,
    2px 3px 0px inset rgb(255, 248, 236);
}
.button:active {
  box-shadow: inset 3px 4px 0px black;
  border: 0px 0px;
}

button > * {
  max-height: 1em;
  max-width: 0.8em;
}

a {
  width: fit-content;
}
